          <v-btn icon to="/en/" router>EN</v-btn>
        </v-card-actions>
        <v-card-title v-if="lang === 'en'">
            <h1>Dmitrii Gerasimenko</h1>
        </v-card-title>
        <v-card-title primary-title v-if="lang === 'ru'">
            <h1>Дмитрий Герасименко</h1>
        </v-card-title>
        <v-card-text class="pt-0" v-if="lang === 'en'">
