            <p>I was born in Siberia, <a rel="nofollow" href="https://goo.gl/maps/ZHvUU1vWz4p" target="_blank">Novisibirsk city</a>.
                Now I am living in <strong>Moscow</strong>.</p>
            <p>I watch a lot of movies and series.</p>
            <p>I am <a href="http://www.mytravelmap.xyz/u/fb1644475605585566">travelling a lot</a>,
              also cycling, skating, skiing.</p>
            <p>I am passionate about the music and play piano and guitar very well.</p>
            <p>I was a Developer at <a rel="nofollow" href="http://yandex.ru">Yandex</a>,
                then I was a System Analyst at <a rel="nofollow" target="_blank" href="http://sberbank.ru">Sberbank</a>.</p>
            <p>I am now the <strong>Software Engineer at <a rel="nofollow" target="_blank" href="http://kindbeetle.ru">Kindbeetle</a></strong>.</p>
            <p>I write code, administer databases, and deploy applications to the cloud infrastructure.</p>
        </v-card-text>
        <v-card-text class="pt-0" v-if="lang === 'ru'">
          <p>Разработчик веб-приложений (80% - бекэнд, 20% - фронтенд).</p>
          <p>Родился в городе Новосибирске. Сейчас проживаю в Москве.</p>
          <p>Много <a href="http://www.mytravelmap.xyz/u/fb1644475605585566">путешествую</a>, катаюсь на велосипеде, роликах и горных лыжах.</p>
          <p>Люблю смотреть зарубежные сериалы (и успешно подсаживаю на них своих друзей и знакомых).</p>
          <p>Не мыслю свою жизнь без музыки. Свободно играю на ф-но и гитаре.</p>
          <p>Был разработчиком в <a href="http://yandex.ru/">Яндексе</a>,
            работал аналитиком в <a href="http://sberbank.ru">Сбербанке</a>.</p>
          <p>Сейчас <strong>я - старший разработчик в <a href="http://kindbeetle.ru/">Kindbeetle</a></strong>.</p>
          <p>Пишу код, управляю серверами и разворачиваю веб-приложения в облачные инфраструктуры.</p>
        </v-card-text>
      </v-card>

      <v-card class="mt-1">
        <v-card-title v-if="lang === 'en'">
          <h3 class="mb-0">Skills</h3>
        </v-card-title>
        <v-card-title v-if="lang === 'ru'">
          <h3 class="mb-0">Навыки & Умения</h3>
        </v-card-title>
