          <v-chip v-for="item in skills" :key="item">
            {{ item.toUpperCase() }}
          </v-chip>
        </v-card-text>
      </v-card>

      <v-card class="mt-1">
        <v-card-title class="pb-1" v-if="lang === 'en'">
          <h3 class="mb-0">Contacts</h3>
