          <v-layout justify-start>
            <v-btn icon :href="c.href" target="_blank" v-for="c in contacts" :key="c.icon">
              <v-icon class="fa-lg">{{ c.icon }}</v-icon>
            </v-btn>
          </v-layout>
        </v-card-text>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  name: 'home-page',
  props: ['lang'],
  data () {
    return {
      skills: [
        'python', 'django', 'javascript', 'nodejs', 'reactjs', 'mobxjs', 'angular2', 'vue.js', 'typescript',
        'ionic2', 'postgresql', 'mysql', 'mssql', 'firebase', 'redis', 'docker', 'terraform', 'vagrant'
      ],
      contacts: [
        { href: 'https://github.com/kidig', icon: 'fa-github' },
        { href: 'https://www.linkedin.com/in/gerasimenko/', icon: 'fa-linkedin' },
        { href: 'https://facebook.com/g7nko', icon: 'fa-facebook' }
      ]
    }
  }
}
</script>



